import "./Dest.css";

export default function Dest({imgText, title, color}) {
    return (
        <>
        <div className="dest" style= {{backgroundImage: `url($image;)`}}>
        <h2 style={{ color: color}} className="destTitle">{title}</h2>
        <p className="destText">{imgText}</p>
        </div>
        </>
    )
}